body {
    font-family: 'Lucida Sans Unicode', serif;
}

h1 {
    text-align: center;
    font-size: 50px;
}

.clickable {
    cursor: pointer;
}

.table {
    margin: 0 auto;
    border-spacing: 0 0;
    display: table;
    border-collapse: separate;
}

.box {
    height: 70px;
    width: 70px;
    background-color: #1990ff;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.circle {
    height: 60px;
    width: 60px;
    border-radius: 100px;
}

.white {
    background-color: white;
}

.red {
    background-color: red;
    -webkit-transition: background-color 0.5s;
    transition: background-color 0.5s;
}

.yellow {
    background-color: yellow;
    -webkit-transition: background-color 0.5s;
    transition: background-color 0.5s;
}
.cell {
    display: table-cell;
    vertical-align: inherit;
}

.row {
    display: table-row;
    vertical-align: inherit;
    border-color: inherit;
}

.button {
    background-color: #303030;
    color: #50C8FF;
    height: 30px;
    width: 100px;
    border-radius: 5px;
    margin: 0 auto 25px;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    cursor: pointer;
    -webkit-transition: background-color 0.5s, color 0.5s;
    transition: background-color 0.5s, color 0.5s;
}

.button:hover {
    background-color: #50C8FF;
    color: #303030;
    -webkit-transition: background-color 0.5s, color 0.5s;
    transition: background-color 0.5s, color 0.5s;
}

.message {
    text-align: center;
    font-size: 17px;
}
